import './App.scss';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import banner from '../../imgs/banner.jpg';
import resonImg from '../../imgs/img_01.png';
import cuLogo from '../../imgs/logo-alone.png';
import ipfsLogo from '../../imgs/ipfs-logo.jpg';
import btcIcon from '../../imgs/bitcoin.png';
import ethIcon from '../../imgs/ethereum.png';
import cruIcon from '../../imgs/crust.png';
import bzzIcon from '../../imgs/ethereumswarm.png';
import b_1 from '../../imgs/i-1.png';
import React, { useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { WOW } from 'wowjs';
function App() {
  let { t } = useTranslation();

  useEffect(() => {
    var wow = new WOW({
      boxClass: "wow",    //需要执行动画的元素的 class
      animateClass: "animated", // animation.css 动画的 class
      offset: 0, // 距离可视区域多少开始执行动画
      mobile: true, // 是否在移动设备上执行动画
      live: true // 异步加载的内容是否有效
    })
    wow.init();
  }, []);
  return (
    <div className="index">
      <Header></Header>
      <div className='content'>
        <div className='nav'>
          <img width='100%' src={banner} alt="" />
        </div>
        <div className='contact flex-m'>

          <div className='title wow bounceInLeft'>{t("index.title_1")}</div>
          <div className='flex-1 wow bounceInRight'>
            <p>{t("index.title_1_1")}</p>
            <p>{t("index.title_1_2")}</p>
          </div>
        </div>

        <div className='business tc'>
          <div className='title bounceInUp wow'>{t("index.title_2")}</div>
          <div className='sub-title bounceInDown wow'>
            <p>{t("index.title_2_1")}</p>
            <p>{t("index.title_2_2")}</p>
          </div>
          <div className="b-img">
            <img width='100%' src={b_1} alt="" />
          </div>
        </div>

        <div className='suc-case'>
          <div className='title'>投资孵化</div>
          <div className="box flex-m">
            <div className='item flex-1'>
              <div className='title'>Taurus</div>
              <div className='sub-tit'>Concentrated in crypto currency hash service</div>
              <div className='img-box flex-m'>
                <img src={btcIcon} alt="" />
                <img src={ethIcon} alt="" />
                <img src={ipfsLogo} alt="" />
                <img src={cruIcon} alt="" />
                <img src={bzzIcon} alt="" />
              </div>
              
              
            </div>
            <div className='item flex-1'>
              <div className='title'>Capricorn</div>
              <div className='sub-tit'>Mainly in quant related services</div>
              {/* <div className='img-box flex-m'>
                <img src={cuLogo} alt="" />
              </div> */}
              
            </div>
            <div className='item flex-1'>
              <div className='title'>Crowd</div>
              <div className='sub-tit'>Hatching block chain related project e.g. NFT</div>
              <div className='img-box flex-m'>
                <img src={cuLogo} alt="" />
              </div>
              
            </div>
            <div className='item flex-1'>
              <div className='title'>Pisces</div>
              <div className='sub-tit'>DEFI oriented</div>
              {/* <div className='img-box flex-m'>
                <img src={cuLogo} alt="" />
              </div> */}
              
            </div>
          </div>
        </div>

        <div className='reason flex-m'>
          <div className='flex-1 img-outer wow bounceInLeft'>
            <img src={resonImg} alt="" />
          </div>
          <div className='flex-1 wow bounceInRight'>
            <div className='title'>{t("index.title_3")}</div>
            <div className='sub-title'>{t("index.title_3_1")}</div>
            <div className='text'>- {t("index.title_3_1_1")}</div>
            <div className='text'>- {t("index.title_3_1_2")}</div>
            <div className='text'>- {t("index.title_3_1_3")}</div>
            <div className='text'>- {t("index.title_3_1_4")}</div>
          </div>

        </div>
      </div>
      <Footer></Footer>
    </div>
  );
}

export default App;
